import './styles/style.scss';
import Home from './Components/Home Page/Home'
import React, {useState, useEffect} from 'react';
import  {Switch, Route} from 'react-router-dom'
import ComposerPage from './Components/Home Page/ComposerPage'

function App() {
  const [search, setSearch] = useState('zeus')
  const [wikiExtract, setWikiExtract] = useState([])
  const [wikiImageURL, setWikiImageURL] = useState([])
  const [wikiInfobox, setWikiInfobox] = useState([])
  const [wikiName, setWikiName] = useState([])

 /*  const url = `https://en.wikipedia.org/w/api.php?action=query&format=json&prop=images%7Cextracts%7Crevisions&titles=Miles%20Davis&formatversion=latest&imlimit=3&exlimit=1&rvprop=content&rvslots=*&rvsection=0&redirects&origin=*`; */

/*  https://github.com/tomer8007/wikipedia-to-json ------- WIKI-To-JSON library to parse HTML  */


 
  async function makeSearch(){
      const url = `https://en.wikipedia.org/w/api.php?action=query&format=json&prop=revisions%7Cpageimages%7Cextracts&generator=search&rvprop=content&rvslots=*&rvsection=0&piprop=thumbnail%7Cname&pithumbsize=500&gsrsearch=${search}&gsrlimit=1&gsrsort=relevance&origin=*`;


      if(search){
        const response = await fetch(url)
        const jsonRes = await response.json()

        const wikiExtract = Object.entries(jsonRes.query.pages)[0][1].extract
        const wikiImageURL = Object.entries(jsonRes.query.pages)[0][1].thumbnail.source
        const wikiName = Object.entries(jsonRes.query.pages)[0][1].title

      setWikiExtract(wikiExtract)
      setWikiImageURL(wikiImageURL)
      setWikiInfobox(wikiInfobox)
      setWikiName(wikiName)
      setSearch(search)
      }
  }

  useEffect(() => {
      makeSearch();
  }, []);


  return (
    <div className="App">

      <Home />
    </div>
  );
}

export default App;
